<template>
	<div class="pdct-l-wp layout">
		<TheCompiler :tabs='tabs' @changeTab='handleChangeTab'></TheCompiler>
		<TheContentList :message='message' @toDetail='handleToDetail'></TheContentList>
	</div>
</template>

<script>
	import TheCompiler from '../components/product/list/TheCompiler.vue'
	import TheContentList from '../components/product/list/TheContentList' 
	export default {
		data() {
			return {
				tabs: [],
				message: []
			};
		},
		methods: {
			handleChangeTab(item){
				this.tabs.find((tab,index)=>tab!=item&&tab.active).active = false
				item.active = true
				this.$router.push('/37duc/product/list/'+item.id)
				this.getMessage(item.id, 1, 6)
				.then(data => {
					this.renderList(data.data)
				})
			},
			handleToDetail(id){
				this.$router.push('/shop/product/detail/'+ id)
			},
			getMessage(productCategoryId, page, limit){
				return new Promise((resolve, reject) => {
					this.$api.product.getProductListWidthType({
						productCategoryId: productCategoryId,
						page: page,
						limit: limit
					}).then(data=>resolve(data))
				})
			},
			renderList(data){
				let message = [];
				data.forEach(val => {
					let item = {}
					item.name = val.name;
					item.marks = [val.feature1, val.feature2, val.feature3];
					item.price = val.price;
					item.character = []
					val.parameter_values[0].entries.forEach(entry => item.character.push({
						name: entry.name,
						limit: entry.limit
					}))
					item.insure_condition = val.insure_condition
					item.id = val.id;
					message.push(item)
				})
				this.message = message
			}
		},
		computed: {
			
		},
		components: {
			TheCompiler,
			TheContentList
		},
		created(){
			this.$api.product.getProductType()
			.then((data) => {
				data.forEach((v,k)=>this.tabs.push({
					tabTxt: v.name,
					active: k==0,
					id: v.id
				}))
				this.getMessage(this.tabs[0].id, 1, 6)
				.then(data => {
					this.renderList(data.data)
				})
			})
		}
	}
</script>

<style lang="scss" scoped="scoped">
.pdct-l-wp{
	wdith: 1100px;
	margin: 12px auto;
}
</style>
